<template>
  <v-data-table
    :headers="headers"
    :items="userInfo"
    sort-by="nome"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Usuários</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-dialog
          v-model="dialog"
          max-width="700px"
          shaped
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              Novo Cadastro
            </v-btn>
          </template>

          <!-- User Register Modal Start -->
            <v-card shaped>
              <v-card-title>{{ formTitle }}</v-card-title>
              <v-card-text>
                <v-select
                  v-model="editedItem.role"
                  :items="roleOptions"
                  label="Papel"
                  prepend-icon="mdi-account-hard-hat"
                />
                <v-text-field
                  v-model="editedItem.name"
                  label="Nome Completo"
                  prepend-icon="mdi-account-circle"
                />
                <v-text-field
                  v-model="editedItem.cpf"
                  label="CPF" 
                  prepend-icon="mdi-card-text"
                />
                <v-text-field
                  v-model="editedItem.email"
                  label="E-mail"
                  prepend-icon="mdi-email"
                />
                <v-text-field
                  v-model="editedItem.phoneNumber"
                  label="Número de Celular"
                  prepend-icon="mdi-cellphone"
                />
                <v-text-field
                  v-model="editedItem.password"
                  label="Senha" 
                  :type="showPassword ? 'text' : 'password'" 
                  prepend-icon="mdi-lock"
                  :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                  @click:append="showPassword =! showPassword"
                />
                <v-text-field 
                  v-model="editedItem.passwordConfirmation"
                  label="Confirmar Senha" 
                  :type="showPassword2 ? 'text' : 'password'" 
                  prepend-icon="mdi-lock"
                  :append-icon="showPassword2 ? 'mdi-eye' : 'mdi-eye-off'"
                  @click:append="showPassword2 =! showPassword2"
                />
              </v-card-text>
              
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancel
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                >
                Save
                </v-btn>
              </v-card-actions>
            </v-card>
        </v-dialog>
        <!-- User Register Modal End -->

        <!-- Delete Modal -->
        <v-dialog v-model="dialogDelete" max-width="700px">
          <v-card>
            <v-card-title class="text-h6">Desativar Usuário</v-card-title>
            <v-card-text class="red--text">Tem certeza que deseja desativar esse Usuário?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDisable">Cancelar</v-btn>
              <v-btn color="blue darken-1" text @click="disableUserConfirm">Desativar</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- Delete Modal End -->

      </v-toolbar>
    </template>
    
    <template v-slot:[`item.actions`]="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        class="mr-2"
        @click="disableUser(item)"
      >
        mdi-delete
      </v-icon>
    </template>

    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>


<!-- DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR -->

<script>
  export default {
    name: 'UserList',

    data: () => ({
      showPassword: false,
      showPassword2: false,
      dialog: false,
      dialogDelete: false,
      menu: false,
      search: '',
      
      roleOptions: ['Administrador', 'Cliente', 'Colaborador'],
      
      required: [
        v => !!v || 'Este campo é obrigatório',
      ],

      headers: [
        {
          text: 'Nome',
          align: 'start',
          sortable: true,
          value: 'name',
        },
        { text: 'CPF', value: 'cpf' },
        { text: 'Papel', value: 'role' },
        { text: '', value: 'actions', sortable: false },
      ],

      userInfo: [],
      editedIndex: -1,
      
      editedItem: {
        name: '',
        cpf: '',
        role: '',
      },
      
      defaultItem: {
        name: '',
        cpf: '',
        role: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Cadastrar Usuário' : 'Editar Usuário'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDisable()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.userInfo = [
          {
            name: 'ALMEIDA DOS SANTOS',
            cpf: '321.123.455-10',
            role: 'Cliente',
          },
        ]
      },

      editItem (item) {
        this.editedIndex = this.userInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      disableUser (item) {
        this.editedIndex = this.userInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      disableUserConfirm () {
        this.userInfo.splice(this.editedIndex, 1)
        this.closeDisable()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDisable () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.userInfo[this.editedIndex], this.editedItem)
        } else {
          this.userInfo.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>